import React from 'react';
import createReactClass from 'create-react-class';
import classNames from 'classnames';
import ReactCarousel from 'rmc-nuka-carousel';
import omit from 'omit.js';
export default class Carousel extends React.Component {
    constructor(props) {
        super(props);
        this.onChange = (index) => {
            this.setState({
                selectedIndex: index,
            }, () => {
                if (this.props.afterChange) {
                    this.props.afterChange(index);
                }
            });
        };
        this.state = {
            selectedIndex: this.props.selectedIndex,
        };
    }
    render() {
        const { className, prefixCls, dotStyle, dotActiveStyle, infinite, selectedIndex, beforeChange, dots, vertical, } = this.props;
        const restProps = omit(this.props, ['infinite', 'selectedIndex', 'beforeChange', 'afterChange', 'dots']);
        const newProps = Object.assign({}, restProps, { wrapAround: infinite, slideIndex: selectedIndex, beforeSlide: beforeChange });
        let Decorators = [];
        const current = this.state.selectedIndex;
        if (dots) {
            Decorators = [{
                    component: createReactClass({
                        render() {
                            const { slideCount, slidesToScroll } = this.props;
                            const arr = [];
                            for (let i = 0; i < slideCount; i += slidesToScroll) {
                                arr.push(i);
                            }
                            const dotDom = arr.map(function (index) {
                                const dotCls = classNames({
                                    [`${prefixCls}-wrap-dot`]: true,
                                    [`${prefixCls}-wrap-dot-active`]: index === current,
                                });
                                const _dotStyle = index === current ? dotActiveStyle : dotStyle;
                                return (<div className={dotCls} key={index}>
                  <span style={_dotStyle}/>
                </div>);
                            });
                            return (<div className={`${prefixCls}-wrap`}>
                {dotDom}
              </div>);
                        },
                    }),
                    position: 'BottomCenter',
                }];
        }
        const wrapCls = classNames({
            [className]: className,
            [prefixCls]: true,
            [`${prefixCls}-vertical`]: vertical,
        });
        return (<ReactCarousel {...newProps} className={wrapCls} decorators={Decorators} afterSlide={this.onChange}/>);
    }
}
Carousel.defaultProps = {
    prefixCls: 'am-carousel',
    dots: true,
    arrows: false,
    autoplay: false,
    infinite: false,
    edgeEasing: 'linear',
    cellAlign: 'center',
    selectedIndex: 0,
    dotStyle: {},
    dotActiveStyle: {},
};



