import React from 'react';
import ListView from 'rmc-list-view';
import Icon from '../icon';
const dpr = typeof window !== 'undefined' && window.devicePixelRatio || 2;
ListView.RefreshControl.defaultProps = Object.assign({}, ListView.RefreshControl.defaultProps, { prefixCls: 'am-refresh-control', icon: [
        <div key="0" className="am-refresh-control-pull">
      <span></span>
    </div>,
        <div key="1" className="am-refresh-control-release">
      <span></span>
    </div>,
    ], loading: <Icon type="loading"/>, refreshing: false, distanceToRefresh: 50 / 2 * dpr });
export default ListView.RefreshControl;



