import React from 'react';
import omit from 'omit.js';
class Input extends React.Component {
    constructor(props) {
        super(props);
        this.onInputBlur = (e) => {
            if (!('focused' in this.props)) {
                this.setState({
                    focused: false,
                });
            }
            const value = e.target.value;
            if (this.props.onBlur) {
                this.props.onBlur(value);
            }
        };
        this.onInputFocus = (e) => {
            if (!('focused' in this.props)) {
                this.setState({
                    focused: true,
                });
            }
            const value = e.target.value;
            if (this.props.onFocus) {
                this.props.onFocus(value);
            }
        };
        this.state = {
            focused: props.focused || false,
        };
    }
    componentWillReceiveProps(nextProps) {
        if ('focused' in nextProps) {
            this.setState({
                focused: nextProps.focused,
            });
        }
    }
    componentDidMount() {
        if ((this.props.autoFocus || this.state.focused) && navigator.userAgent.indexOf('AlipayClient') > 0) {
            this.refs.input.focus();
        }
    }
    componentDidUpdate() {
        if (this.state.focused) {
            this.refs.input.focus();
        }
    }
    render() {
        const otherProps = omit(this.props, ['onBlur', 'onFocus', 'focused', 'autoFocus']);
        return (<input ref="input" onBlur={this.onInputBlur} onFocus={this.onInputFocus} {...otherProps}/>);
    }
}
export default Input;



